<script lang="ts">
	import type { ScatterCoords } from "+/modules/types";

	export let flip = false,
			   content = "This is a tooltip",
			   href = null,
			   coords: ScatterCoords;
	
	let width = 120, 
		height = 40,
		svgEle: SVGElement,
		textEle: SVGTextElement;

	// Set tooltip width after text is changed
	$: if (textEle) {
		textEle.textContent = content;
		width = 18 + textEle.getComputedTextLength();
	}

	// Show tooltip when target is set
	$: if (svgEle) {
		if (coords) {
			// Set tooltip position and visibility
			let top = coords.y - coords.radius - height;
			let left = coords.x - (flip ? 0 : width);
			svgEle.style.top = `${top}px`;
			svgEle.style.left = `${left}px`;
			svgEle.style.visibility = "visible";
		}
		else {
			// Hide tooltip
			svgEle.style.visibility = "hidden";
		}
	}

	// Experimenting with actions
	function makeLink(node: Element, href: string) {
		let link: HTMLAnchorElement;

		let createLink = (node: Element, href: string) => {
			let parent = node.parentElement;
			parent.removeChild(node);

			link = document.createElement("a");
			if (href) link.href = href;
			link.appendChild(node);
			parent.appendChild(link);
		};

		// Only wrap node with link if href exists
		if (href) createLink(node, href);

		return { 
			update: (href: string) => {
				if (link) link.href = href
				else if (href) createLink(node, href);
			},
			destroy: () => {}
		};
	}
</script>

<style type="text/scss">
	div {
		position:relative;
	}

	svg {
		fill-rule:evenodd;
		clip-rule:evenodd;
		stroke-linejoin:round;
		stroke-miterlimit:2;
		position:absolute;
		visibility:hidden;

		path, rect {
			fill:rgb(52,52,52);
		}

		path {
			transform:translateX(calc(100% - 8px));
		}

		path.flip {
			transform:translateX(8px) scaleX(-1);
		}

		text {
			font-family:'Calibri', sans-serif;
			font-size:14px;
			fill:white;
		}
	}
</style>

<div>
	<svg width="{width}px" height="40" viewBox="0 0 {width} 40" bind:this={svgEle} on:mouseleave use:makeLink={href}>
		<path d="M0,32l8,8l0,-8l-8,0Z" class:flip />
		<rect x="0" y="0" width="100%" height="32" />
		<text x="8px" y="20.761px" bind:this={textEle} />
	</svg>
</div>