<script lang="ts">
	import type { JoinedRace, ScatterCoords } from "+/modules/types";

	import { formatDate } from "+/modules/shared";
	import ScatterTooltip from "+/components/ScatterTooltip.svelte";

	export let race: JoinedRace,
		coords: ScatterCoords;

	let content: string,
		href: string,
		flip: boolean;

	$: if (race) {
		content = `${race.location}, ${race.country} - ${formatDate(race.when, "d LLL 'at' h:mm a")}`;
		href = `#/race/${race.year}/${race.round}`;
		flip = race.when.getMonth() < 4;
	}
</script>

<style type="text/scss">
</style>

<ScatterTooltip {content} {flip} {coords} {href} on:mouseleave />