<script lang="ts">
	import type { DateTime } from "luxon";
	import type { JoinedRace } from "+/modules/types";

	import { storedDb as db } from "+/stores/datastore";
	import { getJoinedRaces } from "+/modules/queries/measured";
	import { formatDate } from "+/modules/shared";
	import SectionTemplate from "+/components/SectionTemplate.svelte";
	import SeasonChart from "+/components/SeasonChart.svelte";
	import SeasonMap from "+/components/SeasonMap.svelte";
	import SeasonMapSummary from "+/components/SeasonMapSummary.svelte";

	export let params: { year: number, round: number };

	let races: JoinedRace[], 
		firstDay: DateTime, 
		lastDay: DateTime,
		focusedRace: JoinedRace = null,
		chartRace: JoinedRace = null;
	
	// Early reactive statement to get input from chart
	$: focusedRace = chartRace;

	// Get data from params
	$: {
		races = getJoinedRaces($db, params.year);
		firstDay = luxon.DateTime.fromJSDate(races[0].when).minus({ days: 2 });
		lastDay = luxon.DateTime.fromJSDate(races[races.length - 1].when);
		focusedRace = races.find(r => r.round == params.round);
	}

	// Update history without using router
	$: window.history.pushState({ },"", `#/season/${params.year}` + (focusedRace ? `/${focusedRace.round}` : ""));
</script>

<style type="text/scss">
</style>

<SectionTemplate>
	<svelte:fragment slot="header">
		{params.year} Season, 
		{firstDay && formatDate(firstDay, "d LLLL yyyy")} - 
		{lastDay && formatDate(lastDay, "d LLLL yyyy")}
	</svelte:fragment>
	<SeasonChart {races} raceInput={focusedRace} bind:raceOutput={chartRace} />
	<SeasonMap {races} {focusedRace} />
	<SeasonMapSummary 
		name={focusedRace?.name} 
		year={focusedRace ? focusedRace.year : params.year} 
		round={focusedRace ? focusedRace.round : 1}
	/>
</SectionTemplate>
