<script lang="ts" context="module">
	import type { JoinedRace } from "+/modules/types";
	import { formatDate } from "+/modules/shared";
	
	const iconSize: [number, number] = [12, 12];

	const circle = (className: string) => 
		`<svg width="100%" height="100%" viewBox="0 0 32 32" class="${className}">
			<circle cx="16" cy="16" r="16"/>
			<path d="M16,0C24.831,0 32,7.169 32,16C32,24.831 24.831,32 16,32C7.169,32 0,24.831 0,16C0,7.169 7.169,0 16,0ZM16,2C23.727,2 30,8.273 30,16C30,23.727 23.727,30 16,30C8.273,30 2,23.727 2,16C2,8.273 8.273,2 16,2Z"/>
		</svg>`;

	const monthCircles = [
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-jan") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-feb") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-mar") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-apr") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-may") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-jun") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-jul") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-aug") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-sep") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-oct") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-nov") }),
		L.divIcon({ iconSize, className:"marker-circle", html: circle("color-dec") }),
	];

	export class RaceMarker extends L.Marker {
		race: JoinedRace;

		constructor(race: JoinedRace, options?: L.MarkerOptions) {
			let latLng: L.LatLngExpression = [race.lat, race.lng];

			let baseOptions = {
				title: `${race.location}, ${race.country} - ${formatDate(race.when, "d LLL 'at' h:mm a")}`,
				icon: monthCircles[race.when.getMonth()]
			};

			super(latLng, { ...baseOptions, ...options });
			this.race = race;
		}
	}
</script>

<style type="text/scss">
	:global(.marker-circle) {
		:global(svg) {
			fill-rule:evenodd;
			clip-rule:evenodd;
			stroke-linejoin:round;
			stroke-miterlimit:2;
		}

		:global(path) {
			fill:black;
			filter:opacity(0.2);
		}

		:global(circle) {
			fill:currentColor;
		}
	}
</style>
