<script lang="ts">
	import type * as leaflet from "leaflet";
	import type { JoinedRace } from "+/modules/types";
	import { RaceMarker } from "+/components/RaceMarker.svelte";

	export let races: JoinedRace[],
		focusedRace: JoinedRace;

	let mapElement: HTMLDivElement,
		map: leaflet.Map,
		tileLayer: leaflet.TileLayer,
		markerGroup: leaflet.LayerGroup,
		center = { lat: 12, lng: 14 },
		zoom = 1.875,
		showTiles = true;

	// Create map
	$: if (mapElement && !map) {
		map = L.map(mapElement, { center, zoom, zoomSnap: 0.125 });
	}

	// Create layers
	$: if (map && !tileLayer) {
		tileLayer = L.tileLayer(
			"https://stamen-tiles-{s}.a.ssl.fastly.net/toner-lite/{z}/{x}/{y}{r}.png", {
				attribution: 'Map tiles by <a href="http://stamen.com">Stamen Design</a>, <a href="http://creativecommons.org/licenses/by/3.0">CC BY 3.0</a> &mdash; Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
				subdomains: "abcd",
				minZoom: 0,
				maxZoom: 20
				});
		
		if (showTiles) tileLayer.addTo(map);

		markerGroup = L.layerGroup()
			.addTo(map);
	}

	// Add race markers
	$: if (markerGroup) {
		markerGroup.clearLayers();

		for (let race of races) {
			new RaceMarker(race).addTo(markerGroup);
		}
	}

	// Zoom to target
	$: if (markerGroup) {
		if (focusedRace) {
			let marker = (markerGroup.getLayers() as Array<RaceMarker>)
				.find(x => x.race == focusedRace);

			map.setView(marker.getLatLng(), 5);
		}
		else {
			map.setView(center, zoom);
		}
	} 
</script>

<style type="text/scss">
	#season-map {
		border:solid 1px #343434;
		width:768px;
		height:480px;
		margin-bottom:16px;
	}
</style>

<div id="season-map" bind:this={mapElement}></div>